'use client';

import { Search, X } from 'lucide-react';
import { Input } from '@/components/ui/input';
import { Card } from '@/components/ui/card';

interface RiwayatFilterProps {
    searchQuery: string;
    setSearchQuery: (value: string) => void;
}

export default function RiwayatFilter({ searchQuery, setSearchQuery }: RiwayatFilterProps) {
    return (
        <Card className="p-4 border shadow-sm mb-6 bg-white">
            <div className="relative max-w-md">
                <Search className="absolute left-3 top-3 h-4 w-4 text-gray-500" />
                <Input 
                    placeholder="Cari Kode Transaksi..." 
                    className="pl-9 pr-8 bg-gray-50 border-gray-200 focus:bg-white transition-all" 
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                />
                {searchQuery && (
                    <button 
                        onClick={() => setSearchQuery('')}
                        className="absolute right-3 top-3 text-gray-400 hover:text-gray-600"
                    >
                        <X className="h-4 w-4" />
                    </button>
                )}
            </div>
        </Card>
    );
}