'use client';

import { 
    Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter 
} from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Separator } from '@/components/ui/separator';
import { Badge } from '@/components/ui/badge';
import { CheckCircle2, XCircle, Printer, Info } from 'lucide-react';
import QRCode from "react-qr-code";

interface RiwayatStrukDialogProps {
    isOpen: boolean;
    onOpenChange: (open: boolean) => void;
    data: any | null; 
}

export default function RiwayatStrukDialog({ 
    isOpen, 
    onOpenChange, 
    data
}: RiwayatStrukDialogProps) {
    
    if (!data) return null;

    const formatRupiah = (num: number) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(num);

    const formatDate = (dateString: string) => 
        new Date(dateString).toLocaleDateString('id-ID', { 
            weekday: 'long', day: 'numeric', month: 'long', year: 'numeric', hour: '2-digit', minute: '2-digit' 
        });

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent className="sm:max-w-md bg-white p-0 gap-0 overflow-hidden">
                <DialogHeader className="p-6 pb-2">
                    <DialogTitle className="text-center font-bold text-xl">Detail Pesanan</DialogTitle>
                    <div className="text-center text-gray-500 text-xs font-mono mt-1">
                        #{data.kode_transaksi}
                    </div>
                </DialogHeader>

                <div className="p-6 pt-2 bg-gray-50/50">
                    
                    {/* Status Badge */}
                    <div className="flex justify-center mb-6">
                        <Badge variant={data.status === 'selesai' ? 'default' : data.status === 'pending' ? 'outline' : 'destructive'} className="uppercase tracking-wider px-3 py-1">
                            {data.status === 'selesai' ? <CheckCircle2 className="w-3 h-3 mr-1" /> : null}
                            {data.status}
                        </Badge>
                    </div>

                    {/* Jika pending, tampilkan QR Code untuk bayar */}
                    {data.status === 'pending' && (
                        <div className="flex flex-col items-center justify-center mb-6 bg-white p-4 rounded-xl border border-dashed border-gray-300">
                             <QRCode 
                                value={data.kode_transaksi} 
                                size={120}
                                fgColor="#1a1a1a"
                            />
                            <p className="text-[10px] text-gray-400 mt-2 text-center">Scan di kasir untuk pembayaran</p>
                        </div>
                    )}

                    <div className="grid grid-cols-2 text-sm gap-y-2 text-gray-600 mb-4">
                        <span>Tanggal:</span>
                        <span className="text-right font-medium text-gray-900">{formatDate(data.created_at)}</span>
                        
                        <span>Meja:</span>
                        <span className="text-right font-bold text-[#D93F21]">{data.nomor_meja || '-'}</span>
                    </div>

                    <div className="bg-white rounded-lg border border-gray-100 p-3 mb-4 shadow-sm">
                        <div className="space-y-2">
                            {data.details?.map((item: any, idx: number) => (
                                <div key={idx} className="flex justify-between text-sm">
                                    <div>
                                        <span className="font-medium text-gray-800">{item.produk_resto?.nama_produk}</span>
                                        <div className="text-xs text-gray-500">
                                            {item.jumlah} x {formatRupiah(item.harga_saat_transaksi)}
                                            {item.catatan && <span className="italic text-orange-500 ml-1">({item.catatan})</span>}
                                        </div>
                                    </div>
                                    <span className="font-medium text-gray-900">
                                        {formatRupiah(item.subtotal)}
                                    </span>
                                </div>
                            ))}
                        </div>
                        <Separator className="my-2" />
                         <div className="flex justify-between font-bold text-lg text-gray-900">
                            <span>Total</span>
                            <span>{formatRupiah(data.total_harga)}</span>
                        </div>
                    </div>

                    <div className="flex justify-between text-xs text-gray-500 px-1">
                        <span>Metode Pembayaran</span>
                        <span className="font-medium text-gray-900 uppercase">{data.payment_method?.nama_metode}</span>
                    </div>
                </div>

                <DialogFooter className="p-4 border-t bg-gray-50">
                    <Button variant="outline" className="w-full" onClick={() => onOpenChange(false)}>
                        Tutup
                    </Button>
                </DialogFooter>
            </DialogContent>
        </Dialog>
    );
}