'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { useAuth } from '@/hooks/useAuth';

export function useRiwayatPesananPage() {
    const { user } = useAuth({ middleware: 'auth' });
    
    // State Data
    const [transaksis, setTransaksis] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);
    
    // State Filter
    const [searchQuery, setSearchQuery] = useState('');
    
    // State Modal
    const [selectedStruk, setSelectedStruk] = useState<any | null>(null);
    const [isDialogOpen, setIsDialogOpen] = useState(false);

    // 1. Fetch Data
    const fetchHistory = async () => {
        setLoading(true);
        try {
            // Ambil semua transaksi
            // Idealnya backend punya filter ?user_id=... atau ?mode=history
            // Untuk sekarang kita ambil semua lalu filter di client (jika backend belum support filter user)
            const response = await axios.get('/api/transaksiRestos');
            const data = response.data.data ? response.data.data : response.data;
            
            // Filter hanya milik user yang login
            const myTransactions = Array.isArray(data) 
                ? data.filter((t: any) => t.user_id === user?.id || (t.nama_pelanggan && t.nama_pelanggan.includes(user?.name)))
                // Jika user_id di transaksi resto merujuk ke kasir, maka kita filter by nama_pelanggan
                // Tapi idealnya transaksi resto punya field 'customer_id' atau kita cek nama_pelanggan
                // Asumsi: Kita filter berdasarkan nama_pelanggan yang mengandung nama user
                : [];

            // Sort dari yang terbaru
            const sortedData = myTransactions.sort((a: any, b: any) => new Date(b.created_at).getTime() - new Date(a.created_at).getTime());

            setTransaksis(sortedData);
        } catch (error) {
            console.error(error);
            toast.error("Gagal memuat riwayat pesanan.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { 
        if (user) {
            fetchHistory(); 
        }
    }, [user]);

    // 2. Filter Logic (Search Lokal)
    const filteredData = transaksis.filter(item => 
        item.kode_transaksi?.toLowerCase().includes(searchQuery.toLowerCase())
    );

    // 3. Handlers
    const handleViewDetail = (transaksi: any) => {
        setSelectedStruk(transaksi);
        setIsDialogOpen(true);
    };

    return {
        transaksis: filteredData,
        loading,
        searchQuery,
        setSearchQuery,
        selectedStruk,
        isDialogOpen,
        setIsDialogOpen,
        fetchHistory,
        handleViewDetail
    };
}