'use client';

import { format } from 'date-fns';
import { id as localeId } from 'date-fns/locale';
import { Utensils, Calendar, ChevronRight } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Card } from '@/components/ui/card';

interface RiwayatTableProps {
    loading: boolean;
    transaksis: any[]; 
    onViewDetail: (transaksi: any) => void;
}

export default function RiwayatTable({ loading, transaksis, onViewDetail }: RiwayatTableProps) {
    
    const formatRupiah = (num: number) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(Number(num));

    const formatDate = (dateString: string) => 
        format(new Date(dateString), 'dd MMMM yyyy, HH:mm', { locale: localeId });

    if (loading) {
        return (
            <div className="space-y-4 animate-pulse">
                 {[1, 2, 3].map((i) => (
                    <div key={i} className="h-32 bg-gray-200 rounded-[1.5rem]"></div>
                ))}
            </div>
        );
    }

    if (transaksis.length === 0) {
        return (
            <div className="flex flex-col items-center justify-center py-24 bg-white rounded-[2.5rem] border border-dashed border-gray-200 text-center">
                <div className="w-20 h-20 bg-gray-50 rounded-full flex items-center justify-center mb-4">
                    <Utensils className="w-10 h-10 text-gray-300" />
                </div>
                <h3 className="text-xl font-bold text-gray-900">Belum Ada Pesanan</h3>
                <p className="text-gray-500 mt-2 max-w-xs mx-auto text-sm leading-relaxed">
                    Kamu belum pernah memesan makanan. Yuk pesan menu favoritmu sekarang!
                </p>
            </div>
        );
    }

    return (
        <div className="space-y-4">
            {transaksis.map((item) => (
                <Card 
                    key={item.id} 
                    className="group hover:shadow-md transition-all duration-300 border-gray-200 rounded-2xl overflow-hidden bg-white cursor-pointer hover:border-[#D93F21]/50"
                    onClick={() => onViewDetail(item)}
                >
                    <div className="flex flex-col md:flex-row md:items-stretch">
                        
                        {/* BAGIAN KIRI: Info Utama & Preview Item */}
                        <div className="flex-1 p-5 flex flex-col justify-center">
                            <div className="flex flex-wrap items-center gap-3 mb-3">
                                <div className="flex items-center gap-2 text-xs text-gray-500 font-medium bg-gray-50 px-2.5 py-1 rounded-md border border-gray-100">
                                    <Calendar className="w-3.5 h-3.5" />
                                    {formatDate(item.created_at)}
                                </div>
                                <span className="font-mono text-xs font-bold text-gray-400">
                                    #{item.kode_transaksi}
                                </span>
                            </div>

                            {/* Preview Items (Horizontal List) */}
                            <div className="flex flex-wrap gap-2 items-center">
                                {item.details?.slice(0, 3).map((detail: any, idx: number) => (
                                    <div key={idx} className="flex items-center gap-2 text-sm text-gray-700 bg-white border border-gray-200 px-3 py-1 rounded-full shadow-sm">
                                        <span className="text-[10px] font-bold text-white bg-[#D93F21] w-5 h-5 flex items-center justify-center rounded-full">
                                            {detail.jumlah}
                                        </span>
                                        <span className="font-medium text-xs line-clamp-1 max-w-[150px]">{detail.produk_resto?.nama_produk}</span>
                                    </div>
                                ))}
                                {item.details?.length > 3 && (
                                    <span className="text-xs text-gray-400 px-2 font-medium">+{item.details.length - 3} lainnya</span>
                                )}
                            </div>
                        </div>

                        {/* BAGIAN KANAN: Total & Status */}
                        <div className="flex items-center justify-between md:justify-end gap-6 p-5 bg-gray-50/50 border-t md:border-t-0 md:border-l border-gray-100 min-w-[240px]">
                            
                            <div className="text-left md:text-right">
                                <p className="text-[10px] text-gray-400 font-bold uppercase tracking-wider mb-1">Total Bayar</p>
                                <h3 className="text-xl font-extrabold text-[#D93F21]">
                                    {formatRupiah(item.total_harga)}
                                </h3>
                            </div>

                            <div className="flex flex-col items-end gap-2">
                                {item.status === 'selesai' ? (
                                    <Badge className="bg-green-100 text-green-700 hover:bg-green-100 border-none shadow-none px-3 py-1 rounded-full">Selesai</Badge>
                                ) : item.status === 'dibatalkan' ? (
                                    <Badge variant="destructive" className="px-3 py-1 shadow-none rounded-full">Batal</Badge>
                                ) : (
                                    <Badge variant="outline" className="bg-yellow-50 text-yellow-700 border-yellow-200 px-3 py-1 rounded-full">Pending</Badge>
                                )}
                                
                                <Button size="icon" variant="ghost" className="h-8 w-8 text-gray-300 group-hover:text-[#D93F21] hover:bg-white rounded-full transition-colors hidden md:flex">
                                     <ChevronRight className="w-5 h-5" />
                                </Button>
                            </div>

                        </div>
                    </div>
                </Card>
            ))}
        </div>
    );
}